
* {
    margin: 0;
    padding: 0;
}
body {
    font-family: 'Lato', Calibri, Arial, sans-serif;
    background: #f4f4f4 url(./assets/bg.jpg);
    font-weight: 300;
    font-size: 15px;
    color: #333;
    overflow: scroll;
    overflow-x: hidden;
}
h2 {
    color: #18aaf2;
    padding: 0 15px;
}
.red {
    color: red;
}
ul li {
    display: block;
}
.search div input {
    outline: none;
}
.search div {
    background: #fff;
}
.search div {
    background: #fff;
    color: #5a5a5a;
    cursor: pointer;
    font-weight: 700;
    font-size: 18px;
    padding: 5px 10px;
    box-shadow: 3px 3px 16px #333333;
    border-radius: 0.2em;
}
.tag a {
    background: #fff;
    display: block;
    color: #5a5a5a;
    cursor: pointer;
    font-weight: 700;
    font-size: 18px;
    padding: 10px 20px;
    box-shadow: 3px 3px 16px #333333;
    border-radius: 0.2em;
    text-decoration: none;
}
.tag a:hover {
    background: #18aaf2;
    color: #fff;
}
.search {
    margin-top: 20px;
}
.tag {
    margin-top: 20px;
}
.tag:last-child {
    margin-bottom: 20px;
}
.tag .btn-back {
    width: 80px;
    font-size: 14px;
    padding: 12px 15px;
}



