
.detail-panel {
    background-color: #fff;
    min-height: 200px;
    margin-top: 20px;
    margin-bottom: 20px;
    box-shadow: 3px 3px 16px #333333;
    border-radius: 0.2em;
    font-family: '微软雅黑';
    font-weight: 400;
    font-size: 16px;
}
.detail-panel strong {
    font-weight: 700;
    font-size: 18px;
}
table {
    margin-bottom: 20px
}
table th {
    border: 1px solid #bbb;
    padding: 5px;
}
table td {
    border: 1px solid #bbb;
    padding: 5px;
}
.detail-panel {
    padding: 40px;
}
hr {
    border-top: 5px solid #eee;
}
pre {
    color: #e60c94;
    background-color: #fdfdfd;
    border-radius: 3px;
}



